.trips{
  margin: 4rem 6rem;
  color: black;
}
.trips h1{
  font-size: 3rem;
}
.tripcard{
  margin: 3rem;
  display: flex;
  justify-content: space-between;
}
.t-card{
  width: 32%;
  text-align: start;
  box-shadow: 0 0.4rem 1.5rem 2px rgba(0,0,0,0.11);
  border-radius: .5rem;
  cursor: pointer;
  padding: 1rem 0.8rem;
}
.t-image{
  height: 200px;
  overflow: hidden;
  border-radius: 0.5rem;
}
.t-image img{
  width: 100%;
  height: 100%;
  transform: 0.3s ease-in-out;
  border-radius: 0.5rem;
}
.t-image:hover img{
transform: scale(1.3);

}
.t-card h4{
  font-size: 1.3rem;
  padding: 0.9rem 0 0.2rem 0;
}
@media screen and (max-width:850px) {
  .trips{
    margin:4rem 2rem
  }
  .tripcard{
    flex-direction: column;
  }
  .t-card{
    width: 100%;
    margin-bottom: 1.5rem;
  }
}